  height: 100vh;
}

body{
  height: 100vh;
}

main{
  padding-top: 137px;
  height: 100vh !important;
  transition: padding 0.2s ease-in-out;
}

@media screen and (max-width: 740px) {
  main{
    padding-top: 85px;
  }
}

button {
  width: min-content;
  padding: 0.5rem 3rem;
  border-radius: 0.5rem;
  background-color: var(--primary-background-color);
  outline: 1px solid var(--primary-accent-color);
  outline-offset: -3px;
  font-weight: 900;
  color: white;
}

i {
  color: var(--secondary-text-color);
  font-style: normal;
}

a {
  color: white;
  transform-origin: left;
  position: relative;
  width: 100%;
  font-weight: 900;
  padding: 5px;
}

label{
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
  color: var(--primary-text-color);
}

/* Width and Track */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background-color: var(--background-color);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: var(--primary-accent-color);
  border-radius: 6px;
  border: 3px solid var(--background-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: var(--secondary-accent-color);
}

/* Corner */
::-webkit-scrollbar-corner {
  background-color: var(--primary-background-color);
}

/* Basic styling for the tooltip */
[aria-label] {
  position: relative;
}

/* Styling for the tooltip content */
[aria-label]::before {
  content: attr(aria-label);
  /* Use the value of aria-label as the tooltip content */
  position: absolute;
  background-color: var(--primary-background-color);
  color: var(--primary-text-color);
  border-radius: 5px;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out 1s;
  font-size: small !important;
  font-weight: 600;
  padding: 0.25rem 0.75rem;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.8), 0px 5px 25px rgba(43, 43, 43, 0.5);
  z-index: 99999;
  pointer-events: none;
  white-space: nowrap;
}

[label-position="bottom"]::before {
  top: 90%;
  right: 50%;
  transform: translateY(-100%) translateX(120%);
}

[label-position="left"]::before {
  top: 50%;
  left: 0%;
  transform: translateY(-50%) translateX(-100%);
}

[label-position="right"]::before {
  top: 0%;
  left: 100%;
  transform: translateY(10%) translateX(10%);
}

[label-position="top-left"]::before {
  top: 0%;
  right: 100%;
  transform: translateY(-50%) translateX(0%);
}

[label-position="bottom-right"]::before {
  top: 100%;
  left: 100%;
  transform: translateY(-50%) translateX(-50%);
}

/* Show the tooltip on hover */
[aria-label]:hover::before {
  visibility: visible;
  opacity: 1;
}

h3 b {
  color: var(--secondary-accent-color) !important;
}

p b {
  color: purple;
}

p i {
  color: var(--primary-text-color);
}

.stainedGlass {
  display: flex;
  height: min-content;
  width: 100% !important;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.stainedGlass::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(-90deg, var(--secondary-accent-color), var(--primary-accent-color));
  border-radius: 0;
  z-index: -1;
  box-shadow: inset 0 4px 8px rgba(0, 0, 0, 0.2) !important;
}

.stainedGlass h2{
  display: flex;
  align-items: center;
  justify-content: baseline;
  white-space: nowrap !important;
}

.stainedGlasss > {
  width: min-content;
}

.stainedGlass svg{
  display: flex;
  height: 100%;
  width: 30px;
}

.stainedGlass a svg{
  transition: all 0.2s ease-in-out;
}

.stainedGlass a:hover svg{
  transform: scale(125%);
}`, "",{"version":3,"sources":["webpack://./src/styles/global.css"],"names":[],"mappings":"AAAA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,kBAAkB;EAClB,wBAAwB;EACxB,oCAAoC;AACtC;;AAEA;EACE;IACE,iBAAiB;EACnB;AACF;;AAEA;EACE,kBAAkB;EAClB,oBAAoB;EACpB,qBAAqB;EACrB,iDAAiD;EACjD,8CAA8C;EAC9C,oBAAoB;EACpB,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,kCAAkC;EAClC,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,sBAAsB;EACtB,kBAAkB;EAClB,WAAW;EACX,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,WAAW;EACX,gCAAgC;AAClC;;AAEA,oBAAoB;AACpB;EACE,WAAW;AACb;;AAEA;EACE,yCAAyC;AAC3C;;AAEA,WAAW;AACX;EACE,6CAA6C;EAC7C,kBAAkB;EAClB,yCAAyC;AAC3C;;AAEA,oBAAoB;AACpB;EACE,+CAA+C;AACjD;;AAEA,WAAW;AACX;EACE,iDAAiD;AACnD;;AAEA,kCAAkC;AAClC;EACE,kBAAkB;AACpB;;AAEA,oCAAoC;AACpC;EACE,yBAAyB;EACzB,uDAAuD;EACvD,kBAAkB;EAClB,iDAAiD;EACjD,gCAAgC;EAChC,kBAAkB;EAClB,kBAAkB;EAClB,UAAU;EACV,uCAAuC;EACvC,2BAA2B;EAC3B,gBAAgB;EAChB,wBAAwB;EACxB,8EAA8E;EAC9E,cAAc;EACd,oBAAoB;EACpB,mBAAmB;AACrB;;AAEA;EACE,QAAQ;EACR,UAAU;EACV,6CAA6C;AAC/C;;AAEA;EACE,QAAQ;EACR,QAAQ;EACR,6CAA6C;AAC/C;;AAEA;EACE,OAAO;EACP,UAAU;EACV,0CAA0C;AAC5C;;AAEA;EACE,OAAO;EACP,WAAW;EACX,0CAA0C;AAC5C;;AAEA;EACE,SAAS;EACT,UAAU;EACV,4CAA4C;AAC9C;;AAEA,8BAA8B;AAC9B;EACE,mBAAmB;EACnB,UAAU;AACZ;;AAEA;EACE,+CAA+C;AACjD;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,sBAAsB;EACtB,kBAAkB;EAClB,UAAU;EACV,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,YAAY;EACZ,+FAA+F;EAC/F,gBAAgB;EAChB,WAAW;EACX,yDAAyD;AAC3D;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,yBAAyB;EACzB,8BAA8B;AAChC;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,sBAAsB;AACxB","sourcesContent":["#root{\n  height: 100vh;\n}\n\nbody{\n  height: 100vh;\n}\n\nmain{\n  padding-top: 137px;\n  height: 100vh !important;\n  transition: padding 0.2s ease-in-out;\n}\n\n@media screen and (max-width: 740px) {\n  main{\n    padding-top: 85px;\n  }\n}\n\nbutton {\n  width: min-content;\n  padding: 0.5rem 3rem;\n  bo