  --primary-hue-theme-r: 255;
  --primary-hue-theme-g: 200;
  --primary-hue-theme-b: 0;
  --primary-hue-theme-a: 1;

  --secondary-hue-theme-r: 0;
  --secondary-hue-theme-g: 255;
  --secondary-hue-theme-b: 174;
  --secondary-hue-theme-a: 1;

